/* Oscillator incremental buttons */

.oscAdjust {
    display: block;
    border: 0;
    margin: 0 auto;
    width: var(--s5);
    height: var(--s5);
    // padding: var(--s0) var(--s2);
    font-size: var(--s2);
    font-weight: 600;
    text-transform: uppercase;
    color: var(--blue);
    background: transparent;
    border: 5px solid var(--blue);
    border-radius: 25px;
    &:first-of-type {
        margin-bottom: var(--s-1);
    }
}
